import React from 'react';
import PropTypes from 'prop-types';

import Typography from '@material-ui/core/Typography';

const Descripcion = ({ nombre, precio, descripcion, descripcionBreve}) => {
    return (
        <React.Fragment>
            <Typography variant="overline" align="center" component="h1" id="aria-title">
                VELVET chocolates
            </Typography>                  
            <Typography variant="h5" align="center" component="h2">
                {nombre}
            </Typography>               
            <Typography variant="overline" align="center" paragraph component="p">
                {`$ ${precio}`}
            </Typography>
            <Typography variant="body1" align="left" style={{marginTop: 16}} paragraph>
                {descripcion}
            </Typography>
            <Typography variant="subtitle2" align="left" gutterBottom>
                Ingredientes
            </Typography>
            <Typography variant="body1" align="left" paragraph>
                Chocolate con leche, chocoate semiamargo, crema, miel, manteca de cacao.                    
            </Typography>                  
            <Typography variant="body1" align="left" >                  
                <i>{descripcionBreve}</i>
            </Typography>            
        </React.Fragment>
    );
};

Descripcion.propTypes = {
    nombre: PropTypes.string.isRequired,
    precio: PropTypes.number.isRequired,
    descripcion: PropTypes.string.isRequired,
    descripcionBreve: PropTypes.string.isRequired,
};

export default Descripcion;